/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Open Sans", sans-serif;
  font-size: 1.6rem;
  background-color: #f0f0f0;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

img {
  max-width: 100%;
  display: block;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

h1, h2, h3 {
  margin: 0;
  line-height: 1.2;
}

h1 {
  font-size: 4.4rem;
  font-family: "Roboto", cursive;
}

h2 {
  font-size: 4rem;
  font-family: "Roboto", cursive;
}

h3 {
  font-size: 3rem;
}

[class$=heading] {
  margin: 2rem 0;
}

a {
  text-decoration: none;
  color: #000;
}

p {
  color: #000;
  font-size: 1.6rem;
  line-height: 1.8;
}

.header {
  background-image: linear-gradient(to right, rgba(59, 72, 98, 0.9) 0%, rgba(59, 72, 98, 0.9) 100%), url(../img/header_bg.jpg);
  background-size: cover;
  background-position: bottom center;
  padding: 3rem 0;
  position: relative;
}
@media (min-width: 768px) {
  .header {
    padding: 3rem 0 20rem 0;
  }
}
.header__barra {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .header__barra {
    flex-direction: row;
    justify-content: space-between;
  }
}
.header__logo {
  width: 25rem;
}
@media (min-width: 768px) {
  .header__contenido {
    margin-top: 10rem;
    max-width: 50rem;
  }
}
.header__texto {
  color: #FFFFFF;
  display: none;
}
@media (min-width: 768px) {
  .header__texto {
    display: block;
    text-align: left;
  }
}
.header__heading {
  color: #FFFFFF;
  text-align: center;
}
@media (min-width: 768px) {
  .header__heading {
    text-align: left;
  }
}
.header__imagen {
  display: none;
}
@media (min-width: 768px) {
  .header__imagen {
    display: block;
    position: absolute;
    right: 0;
    bottom: -5rem;
    max-width: 35rem;
    border-top-left-radius: 2rem;
    border-bottom-left-radius: 5rem;
  }
}
@media (min-width: 1200px) {
  .header__imagen {
    max-width: 60rem;
  }
}

.navegacion {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .navegacion {
    flex-direction: row;
    gap: 2rem;
    margin-top: 0;
  }
}
.navegacion__link {
  color: #FFFFFF;
  font-size: 1.8rem;
  margin-bottom: 2rem;
}
.navegacion__link:last-of-type {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .navegacion__link {
    margin-bottom: 0;
  }
}

.propiedades {
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .propiedades {
    margin-top: 10rem;
  }
}
.propiedades__heading {
  font-weight: 400;
}
.propiedades__heading span {
  font-weight: 700;
}
.propiedades__heading::after {
  content: "";
  display: block;
  height: 1rem;
  width: 15rem;
  margin-top: 1rem;
  background-image: linear-gradient(to right, #3b4862 0%, #3b4862 50%, #6277a1 50%, #6277a1 100%);
}
.propiedades__grid {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .propiedades__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.propiedad {
  background-color: #FFFFFF;
}
.propiedad__imagen {
  aspect-ratio: 10/6;
}